import React from 'react'

const CodeSandbox = ({ id }) => {
  return (
    <iframe
      title={`Codesandbox example ${id}`}
      src={`https://codesandbox.io/embed/${id}?autoresize=1&fontsize=14&hidenavigation=1`}
      style={{
        width: '100%',
        height: 500,
        border: 0,
        borderRadius: 4,
        overflow: 'hidden',
        marginBottom: 24,
      }}
      sandbox="allow-modals allow-forms allow-popups allow-scripts allow-same-origin"
    />
  )
}

export default CodeSandbox
